*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body {
  background-color: #f8f8f8;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 15px;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
}

.lists {
  list-style: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}



a {
  text-decoration: none;
}

.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.navigation {
  display: none;
}

.navigation a {
  position: relative;
}

.navigation a:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #9bb537;
  transition: transform .3s ease-in-out;
  position: absolute;
  bottom: 30px;
  left: 0;
  transform: scaleX(0);
}

.header-nav-links {
  color: #050505;
  padding-top: 43px;
  padding-bottom: 43px;
  font-family: Inter;
  font-size: 12px;
  font-weight: 500;
}

.header-nav-links:hover, .header-nav-links:focus {
  color: #9bb537;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.home-marg {
  margin-right: 16px;
}

.logo-tasty {
  color: #9bb537;
  letter-spacing: -.64px;
  padding-top: 40px;
  padding-bottom: 40px;
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
}

.logo-treats {
  color: #050505;
  letter-spacing: -.64px;
  padding-top: 43px;
  padding-bottom: 43px;
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
}

.header-items {
  align-items: center;
  display: flex;
}

.header-icons {
  align-items: center;
  gap: 14px;
  margin-left: auto;
  display: flex;
}

.header-shopping-btn {
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.shopping-icon-use:hover, .shopping-icon-use:focus {
  stroke: #9bb537;
  transition: all .4s;
}

.mob-menu-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

.mobile-menu-icon {
  stroke: #050505;
}

.mobile-menu-icon:hover {
  stroke: #9bb537;
  transition: all .4s;
}

.mobile-menu-icon:focus {
  stroke: #9bb537;
}

.header-switch {
  width: 47px;
  height: 20px;
  border-radius: 10px;
  display: flex;
  position: relative;
  box-shadow: 7px 5px 15px rgba(160, 158, 160, .4);
}

.header-switch input {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: #cecdcd;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #f8f8f8;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.header-switch input:checked + .header-slider {
  background: linear-gradient(#9bb537, #9bb537);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.header-switch input:checked + .header-slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.mobile-menu {
  height: 100%;
  width: 200px;
  z-index: 999;
  background-color: #9bb537;
  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(300%);
}

.mobile-menu.open {
  transform: translateX(0);
}

.mob-menu-btn-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mob-menu-btn-close .mob-menu-close {
  transition: transform .3s ease-in-out;
}

.mob-menu-btn-close:hover {
  transform: rotate(45deg);
}

.mob-menu-btn-close:hover .mob-menu-close {
  transform: scale(1.2);
}

.mob-menu-close-icon {
  stroke: #fff;
}

.mob-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 16px;
  padding-top: 200px;
  padding-bottom: 566px;
  display: flex;
}

.header-list-item {
  color: #fff;
  justify-content: center;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.active-header-list-item {
  font-weight: 500;
}

.switch {
  width: 46px;
  height: 20px;
  position: absolute;
  bottom: 30px;
  left: 20px;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: rgba(248, 248, 248, .6);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #f8f8f8;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background: #9bb537;
}

input:checked + .slider:before {
  transform: translateX(26px);
  box-shadow: 4px 4px 15px rgba(243, 243, 243, .25);
}

.nav-link-active {
  color: #9bb537;
}

.mob-nav-link-active {
  color: #050505;
  position: relative;
}

.shopping-icon-use {
  fill: #f8f8f8;
  stroke: #050505;
}

